import React from 'react';
import Table from './Table';
import Form from './Form';

class App extends React.Component{
    state={
            characters:[
                {name: 'Mr Thanh',job: 'Programming'},
                {name: 'Ms Duy',job: 'Data Processing'},
                {name: 'Mr Khang',job: 'Accountant'},
                {name: 'Mrs Thao',job: 'Model Fashion'}
            ],
            isAsc: true
    }

    removeCharacter=(index)=>{
        const {characters}=this.state;//const characters=this.state.characters
        this.setState({
            characters:characters.filter((character, i) => {// Khi index=i=1, index=0,2,3 will have in new array. index=1 is not
                return i !==index;
            })
        });
    }

    //Những cách viết kiểu này cũng cần nắm đặn nữa đọc code người khác nè
    handleSubmitForm=(character)=>{ // handleSubmitForm(character){...}
        this.setState(
            {
                //Spread syntax to understand
                characters:[...this.state.characters,character]
            }
        );
    }

    handleTest=()=>{//Nếu khai báo kiểu function(){..} lỗi underfined, đơn giản tại vì nó là phương thức trong class
    //phương thức trong class thì mặc định không được liên kết mà phải thông qua bind ở constructor
        const isOrder=this.state.isAsc;
        //alert(isOrder ? 'Asc' : 'Desc');

        let orderArray=Swap(this.state.characters,isOrder);
    
        this.setState({
            characters: orderArray,
            isAsc: !this.state.isAsc
        });
    }

    render(){
        const name="Guest!";
        let {characters}=this.state;

        return (
            <div className="container">
                <h1>Hello, {name} Welcome to myApp React</h1>
                <Table 
                charactersData={characters}
                removeCharacter={this.removeCharacter}
                test={this.handleTest}
                stateImg={this.state.isAsc}
                />

                <Form handleSubmit={this.handleSubmitForm}
                />
            </div>
        );
    }
}

function Swap(data, isOrder){
    if(isOrder){
        return data.sort(
            function(a, b){
                const jobA=a.job.toLocaleLowerCase();
                const jobB=b.job.toLocaleLowerCase();

                if(jobA < jobB){
                    return -1;
                }
                return 0;
            }
        );
    }
    else{
        return data.sort(
            function(a, b){
                const jobA=a.job.toLocaleLowerCase();
                const jobB=b.job.toLocaleLowerCase();

                if(jobA > jobB){
                    return -1;
                }
                return 0;
            }
        );
    }
}
export default App;