import React from 'react';

//split content of class Table become TableHeader and TableBody=> reuse
const TableHeader=(props)=>{
    const disImg=props.disImg;
    const urlImg=disImg ? <img src={require('./image/down.png')} alt="test"/>
    : <img src={require('./image/up.png')} alt="test"/>;
    return (
        <thead>
            <tr>
                <th>Name</th>
                <th onClick={props.sort}>Job
                {urlImg ? urlImg : ''}
                </th>
                <th>Remove</th>
            </tr>
        </thead>
    );
}

const TableBody=(props)=>{
    const rows=props.charactersData.map((row, index) => {
        return (
            <tr key={index}>
                <td>{row.name}</td>
                <td>{row.job}</td>
                <td onClick={()=>props.removeCharacter(index)}
                className="button"
                >Delete</td>
            </tr>
        );
    });
    return (<tbody>{rows}</tbody>);
}

class Table extends React.Component{
    render(){
        const {
            charactersData,// same: charactersData=this.props.charactersData, ES6
            removeCharacter,// same: removeCharacter=this.props.removeCharacter, ES6
            test,
            stateImg
        }=this.props;
        return(
            <table>
                <TableHeader 
                sort={test}
                disImg={stateImg}
                />
                <TableBody 
                charactersData={charactersData}
                removeCharacter={removeCharacter}
                />
            </table>
        );
    }
}

export default Table;